
html,
.container {
  width: 100%;
}
body {
  margin: 0;
  padding: 0;
}
.container {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0.3rem 0.3rem;
  text-align: justify;
  font-size: 0.28rem;
  color: #333;
  line-height: 0.42rem;
}
.title {
  color: #333;
  font-size: 0.3rem;
  font-weight: bolder;
  margin: 0.1rem 0;
}
.title.center {
  text-align: center;
  font-size: 0.35rem !important;
}